body,
html,
#root {
  height: 100%;
}

html {
  font-size: 18px;
}

h1 {
  padding: 2rem 0;
  margin: 0;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-size: 4rem;
  text-shadow: 4px 4px 0px #af0a83;
}

h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 1.55rem;
}

.App {
  height: 100vh;
  padding: 0 17% 10% 17%;
  background-image: linear-gradient(rgba(34, 34, 34, 0.5), rgba(0, 0, 0, 0.5)),url('./background_photo_desktop.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
}

.App-playlist {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: 1020px) {
  .App-playlist {
    align-items: center;
    flex-direction: column;
  }
}